export type TypeOfMode = number;

// 0 
export const NoMode = 0b0000;
// 1 
export const StrictMode = 0b0001;
// 10 
export const BlockingMode = 0b0010;
// 100 
export const ConcurrentMode = 0b0100;
// 1000 
export const ProfileMode = 0b1000;
